.UserMenu {
  display: flex;
}

.logOutButton {
  margin-left: 1em;
  background-color: #333;
  color: #ffffff;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
  transition: background-color 0.5s ease-in;
}

.logOutButton:hover {
  background-color: #ef1814;
}

.logOutButton:active {
  background-color: #e92020;
}

.p {
  position: relative;
  color: #000000;
  font-size: 15px;
  font-weight: 600;
}
